<template>
  <div>
    <router-link to="/">ANASAYFA</router-link>
    <router-link to="/person">KİŞİ</router-link>

    <router-link to="/" v-if="!user">ü</router-link>
    <router-link to="/" v-if="!user">y</router-link>
    <a href="javascript:void(0)" v-else @click="logout" >ÇIKIŞ</a>
  </div>
</template>
<script>
  import { mapGetters ,mapActions } from 'vuex'

  export default {
    computed: {
      ...mapGetters({
        UserInfo:"UserInfo"
      }),
      ...mapActions({
        UserLogout:"UserLogout"
      }),
      user(){
        return this.UserInfo
      }
    },
    methods:{
      logout(){
        this.$store.dispatch('UserLogout');
      }
    }
  }
</script>



