	      text2: "\n            DAHA \xD6NCE S\u0130TEM\u0130ZE \xDCYE OLMU\u015ESANIZ VE \u015E\u0130FREN\u0130Z\u0130 HATIRLAMIYOR YA DA \u015E\u0130FREN\u0130Z\u0130 DE\u011E\u0130\u015ET\u0130RMEK \u0130ST\u0130YORSANIZ; \xDCYE G\u0130R\u0130\u015E\u0130 B\xD6L\xDCM\xDCNDEK\u0130 \u015E\u0130FREM\u0130 UNUTTUM ALANINA TIKLAYARAK \xDCYEL\u0130\u011E\u0130N\u0130Z\u0130 OLU\u015ETURURKEN KULLANDI\u011EINIZ E-POSTA ADRES\u0130N\u0130Z\u0130 YAZARAK \"G\xD6NDER\" TU\u015EUNA BASINIZ. YEN\u0130 \u015E\u0130FRE E-POSTA ADRES\u0130N\u0130ZE G\xD6NDER\u0130LECEKT\u0130R.\n        ",
	      text3: "\n           GRAMOFONEV\u0130.COM\u2019DAN S\u0130PAR\u0130\u015E VEREB\u0130LMEK \u0130\xC7\u0130N \xD6NCEL\u0130KLE \xDCYE OLMANIZ GEREK\u0130YOR. \xDCYE KAYDI \xDCZER\u0130NDEN \xDCYEL\u0130K \u0130\u015ELEMLER\u0130N\u0130Z\u0130 GER\xC7EKLE\u015ET\u0130REB\u0130L\u0130RS\u0130N\u0130Z. \xDCYEL\u0130K B\u0130LG\u0130LER\u0130N\u0130Z\u0130 S\u0130TEYE G\u0130RD\u0130KTEN SONRA \xDCR\xDCN\xDCN\xDCZ\xDC SEPET\u0130N\u0130ZE EKLEMEL\u0130S\u0130N\u0130Z. SEPET\u0130N\u0130ZDEN SATIN AL BUTONU \u0130LE B\u0130R SONRAK\u0130 EKRANA GE\xC7EB\u0130L\u0130R, TESL\u0130MAT VE FATURA ADRES\u0130 B\u0130LG\u0130LER\u0130N\u0130Z\u0130 DOLDURDUKTAN SONRA, \xD6DEME EKRANINA GELEREK S\u0130PAR\u0130\u015E\u0130N\u0130Z\u0130 TAMAMLAYAB\u0130L\u0130RS\u0130N\u0130Z.        ",
	      text4: "\n             S\u0130PAR\u0130\u015ELER\u0130N\u0130Z ORTALAMA 3 \u0130\u015E G\xDCN\xDC \u0130\xC7ER\u0130S\u0130NDE S\u0130ZE TESL\u0130M ED\u0130LECEKT\u0130R.\n        ",
